import * as React from "react"
import SEO from "../../components/seo";
import { Child, ExampleContainer } from "../../components/ExampleComponents";
import SyntaxHighlight from "../../components/SynaxHighlight";
import { alignItems, alignItemsCodeString, alignSelf, alignSelfCodeString, justifyContent, justifyContentCodeString } from "../../data/align-justify";

const flexDirection = [ 'row', 'row-reverse', 'column', 'column-reverse' ];
const codeString = flexDirection.map( dir => `<div class="flex--${ dir }">...</div>\n` )

const Flexbox = () => {
    return (
        <>
            <SEO title={ "Flexbox" }/>
            <h1>Flexbox</h1>
            <div id={ "flex-direction" } className={ "pt--xs" }>
                <h2>Flex Direction</h2>
                { flexDirection.map( direction => (
                    <ExampleContainer key={ direction } className={ `flex--${ direction }` } useHeight={ false }>
                        <Child children={ '1' } fullwidth/>
                        <Child children={ '2' } fullwidth/>
                        <Child children={ '3' } fullwidth/>
                        <Child children={ '4' } fullwidth/>
                    </ExampleContainer>
                ) ) }
                <SyntaxHighlight lang={ 'html' } codeString={ codeString.join( '' ) }/>
            </div>
            <div id={ "flex-fill" } className={ "pt--xs" }>
                <h2>Flex Fill</h2>
                <ExampleContainer useHeight={ false }>
                    <Child className={ "flex--fill" } children={ 'Child with more content than the other' }/>
                    <Child className={ "flex--fill" } children={ 'Child' }/>
                    <Child className={ "flex--fill" } children={ 'Child' }/>
                </ExampleContainer>
                <SyntaxHighlight lang={ 'html' } codeString={ `<div class="flex">
    <div class="flex--fill">...</div>
    <div class="flex--fill">...</div>
    <div class="flex--fill">...</div>
</div>` }/>
            </div>

            <div id={ "flex-grow" } className={ "pt--xs" }>
                <h2>Flex Grow</h2>
                <ExampleContainer useHeight={ false }>
                    <Child className={ "flex--grow" }/>
                    <Child/>
                </ExampleContainer>
                <SyntaxHighlight lang={ 'html' } codeString={ `<div class="flex">
    <div class="flex--grow">...</div>
    <div>...</div>
</div>` }/>

                <ExampleContainer useHeight={ false }>
                    <Child className={ "flex--shrink" }/>
                    <Child fullwidth/>
                </ExampleContainer>
                <SyntaxHighlight lang={ 'html' } codeString={ `<div class="flex">
    <div class="flex--shrink">...</div>
    <div class="w--100">...</div>
</div>` }/>
            </div>

            <div id={ "flex-wrap" } className={ "pt--xs" }>
                <h2>Flex Wrap</h2>
                <ExampleContainer useHeight={ false } className={ "w--25 flex--nowrap" } customLabel={ "flex--nowrap" }>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                </ExampleContainer>
                <ExampleContainer useHeight={ false } className={ "w--25 flex--wrap" } customLabel={ "flex--wrap" }>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                </ExampleContainer>
                <ExampleContainer useHeight={ false } className={ "w--25 flex--wrap-reverse" } customLabel={ "flex--wrap-reverse" }>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                    <Child/>
                </ExampleContainer>
                <SyntaxHighlight lang={ 'html' } codeString={ `<div class="flex--nowrap">...</div>
<div class="flex--wrap">...</div>
<div class="flex--wrap-reverse">...</div>` }/>
            </div>
            <div id={ "align-items" } className={ "pt--xs" }>
                <h2>Align Items</h2>
                { alignItems.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } className={ items.class } isDefault={ items.default }>
                            <Child fullwidth/>
                            <Child fullwidth/>
                            <Child fullwidth/>
                        </ExampleContainer>
                    )
                } ) }
                <SyntaxHighlight lang={ 'html' } codeString={ alignItemsCodeString.join( '' ) }/>
            </div>

            <div id={ "justify-content" } className={ "pt--xs" }>
                <h2>Justify Content</h2>
                { justifyContent.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } className={ items.class } isDefault={ items.default } useHeight={ false }>
                            <Child/>
                            <Child/>
                            <Child/>
                        </ExampleContainer>
                    )
                } ) }
                <SyntaxHighlight lang={ 'html' } codeString={ justifyContentCodeString.join( '' ) }/>
            </div>
            <div id={ "align-self" } className={ "pt--xs" }>
                <h2>Align Self</h2>
                <ExampleContainer>
                    { alignSelf.map( ( items, index ) => {
                        return (<Child key={ index } className={ items.class } children={ items.class } fullwidth/>)
                    } ) }
                </ExampleContainer>
                <SyntaxHighlight lang={ 'html' } codeString={ `<div class="grid">\n${ alignSelfCodeString.join( '' ) }</div>` }/>
            </div>
        </>
    )
}

export default Flexbox
